* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Courier New", Courier, monospace;
}
body {
  background-image: url("../public/beton-card.jpeg");
  padding: 2px;
}
//Сделать индекатор продвижения успеха в тренировке.
hr {
  width: 100%;
  border-radius: 50%;
  border: 1px solid rgb(238, 184, 8);
  opacity: 1;
  box-shadow: 0 3px 5px rgba(0, 0, 0);
  position: relative;
  z-index: 1;
  margin: 0 auto;
}
